.music-card-container {
  align-items: center;
  box-shadow: 0.1px 3px 2px rgb(224, 224, 224);
  display: flex;
  justify-content: space-between;
  padding: 10px;
  width: 800px;
}

.music-card-container img {
  border-radius: 50%;
  width: 60px;
}

.music-card-container span {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 200px;
}
