@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@100;400&family=Merriweather+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@100&family=Merriweather+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@100;400&family=Merriweather+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@100;400&family=Merriweather+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@100;400&family=Merriweather+Sans:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
}

.loading-message-container {
  align-items: center;
  color: #023031;
  display: flex;
  font-family: Epilogue , sans-serif;
  height: 74px;
  justify-content: center;
}

.login-form-container {
  left: 50%;
  margin: -200px -260px;
  position: fixed;
  text-align: center;
  top: 50%;
}

.login-form-container form {
  box-shadow: 0.1px 3px 8px rgb(173, 173, 173);
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  margin-top: 110px;
  padding: 60px;
  grid-row-gap: 12px;
  row-gap: 12px;
  width: 400px;
}

.login-form-container form input {
  height: 32px;
  text-indent: 10px;
  width: 400px;
}

.login-form-container form button {
  background-color: #003be5;
  color: white;
  height: 32px;
  width: 400px;
}

.login-form-container form button:disabled {
  background: rgb(160, 160, 160);
  color: rgb(75, 75, 75);
  opacity: 0.6;
}

header {
  background-color: #023031;
  display: flex;
  flex-wrap: wrap;
  font-family: Merriweather Sans , sans-serif;
  justify-content: space-between;
}

.trybetunes-logo {
  margin-bottom: 16px;
  margin-left: 20px;
  width: 106px;
}

header nav {
  box-shadow: 0.1px 3px 2px rgb(173, 173, 173);
  display: flex;
  width: 100%;
}

.username-container {
  align-items: center;
  background-color: white;
  border-radius: 20px;
  color: #29313d;
  display: flex;
  height: 40px;
  justify-content: flex-start;
  margin-right: 26px;
  margin-top: 24px;
  width: 200px;
}

.username-container img {
  border-radius: 50%;
  margin: 4px;
  width: 30px;
}

.username-container span {
  display: block;
  overflow: hidden;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 140px;
}

header nav > * {
  background-color: #f0f2f5;
  border-right: 2px solid white;
  color: #2fc18c;
  font-weight: 800;
  padding: 16px;
  text-align: center;
  text-decoration: none;
  width: 100%;
}

header nav > *:hover {
  background-color: #036b52;
  color: white;
}

.albums-section {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.albums-section h3 {
  align-self: flex-start;
  color: #001813;
  font-family: Epilogue , sans-serif;
  font-weight: 400;
  padding-bottom: 20px;
}

.albums-cards-container {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  margin: auto;
  margin-bottom: 200px;
  overflow-x: scroll;
  width: 1150px;
}

.albums-cards-container::-webkit-scrollbar {
  width: 16px;
}

.albums-cards-container::-webkit-scrollbar-track {
  background-color: #e4e4e4;
  border-radius: 100px;
}

.albums-cards-container::-webkit-scrollbar-thumb {
  background-color: #3d495c;
  border-radius: 100px;
}

.albums-cards-container div > * {
  color: #3d495c;
  font-family: Verdana , Geneva , Tahoma , sans-serif;
}

.albums-cards-container div a {
  font-weight: 800;
  text-decoration: none;
}

.albums-cards-container div img {
  height: 180px;
  object-fit: cover;
  width: 280px;
}

.search-form-container form {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  margin: auto;
  margin-top: 40px;
  padding: 60px;
  width: 600px;
}

.search-form-container form input {
  border-radius: 6px;
  font-family: Verdana , Geneva , Tahoma , sans-serif;
  font-size: 16px;
  height: 32px;
  text-indent: 10px;
  width: 400px;
}

.search-form-container form button {
  background-color: #003be5;
  color: white;
  height: 38px;
  width: 100px;
}

.search-form-container form button:disabled {
  background: rgb(160, 160, 160);
  color: rgb(75, 75, 75);
  opacity: 0.6;
}

.music-card-container {
  align-items: center;
  box-shadow: 0.1px 3px 2px rgb(224, 224, 224);
  display: flex;
  justify-content: space-between;
  padding: 10px;
  width: 800px;
}

.music-card-container img {
  border-radius: 50%;
  width: 60px;
}

.music-card-container span {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 200px;
}

.album-details-container {
  display: flex;
  justify-content: space-evenly;
  padding: 120px;
}

.album-info-container {
  color: #023031;
  font-family: Epilogue , sans-serif;
}

.album-info-container h3 {
  margin: auto;
}

.album-info-container img {
  border-radius: 4px;
  margin-bottom: 30px;
  width: 300px;
}

.album-songs-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-family: Epilogue , sans-serif;
  grid-row-gap: 10px;
  row-gap: 10px;
}

.album-songs-container div {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.page-favorites {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.page-favorites h3 {
  align-self: flex-start;
  color: #001813;
  font-family: Epilogue , sans-serif;
  font-weight: 400;
  margin-top: 80px;
  padding-bottom: 20px;
}

.favorite-songs-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  grid-row-gap: 16px;
  row-gap: 16px;
}

.user-profile-container {
  color: #3d495c;
  font-family: Verdana , Geneva , Tahoma , sans-serif;
  height: 500px;
  margin: auto;
  margin-top: 60px;
  width: 360px;
}

.user-profile-container p {
  font-size: 18px;
  margin-top: -20px;
}

.user-profile-container img {
  border-radius: 50%;
  width: 80px;
}

.edit-section {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.profile-edit-btn {
  border: 1px solid #003be5;
  border-radius: 5px;
  color: #003be5;
  padding: 6px;
  text-decoration: none;
}

.user-profile-form {
  align-items: center;
  color: #3d495c;
  display: flex;
  flex-direction: column;
  font-family: Verdana , Geneva , Tahoma , sans-serif;
  height: 500px;
  margin: auto;
  margin-top: 60px;
  grid-row-gap: 28px;
  row-gap: 28px;
  width: 360px;
}

.profile-picture-container {
  align-items: baseline;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  justify-content: space-between;
  width: 100%;
}

.user-profile-form label {
  display: flex;
  flex-direction: column;
  font-weight: 800;
  grid-row-gap: 10px;
  row-gap: 10px;
  width: 100%;
}

.user-profile-form input, textarea {
  border: none;
  border-bottom: 1px solid #3d495c;
}

.save-profile-btn {
  background-color: #003be5;
  border-radius: 2px;
  color: white;
  padding: 8px;
  width: 120px;
}

.save-profile-btn:disabled {
  background: rgb(160, 160, 160);
  color: rgb(75, 75, 75);
  opacity: 0.6;
}

.profile-picture {
  border-radius: 50%;
  width: 80px;
}

